@import url('https://fonts.googleapis.com/css?family=Quicksand:400,500,700&subset=latin-ext');


* {
  box-sizing: border-box;
}

a, a:hover {
  text-decoration: none;
}

.icon {
  display: inline-block;
  width: 1em;
  height: 1em;
  stroke-width: 0;
  stroke: currentColor;
  fill: currentColor;
}

.wrapper {
  width: 100%;
  height: auto;
  margin-bottom: 5em;
  margin-top:1em;
  display: flex;
}

.profile-card {
  width: 100%;

  padding:2em;
  margin: auto;
  box-shadow: 0px 8px 60px -10px rgba(13,28,39,0.6);
  background: #fff;
  border-radius: 12px;
  max-width: 700px;
  position: relative;
}

.cards-list{
  text-align: center;
}

.text{
  width: 500px;
}

@media screen and (max-width: 562px) {
  .text{
    width: auto;
  }
}

.t{
  margin-top:-25px;
}