  }
    .progressbar li {
     width:auto;
     font-size: 10px;
     margin-right:12px;
    }
    .progressbar li:before{
      font-size:15px;
    }
  #lorem-ipsum
  {
  margin-left: 20px;
  margin-right:20px;
  }
}

